const Chapter = () => {
  const [chapter, setChapter] = useState({ data: "" });
  const [chapterGot, setChapterGot] = useState(false);
  const [chapterCount, setChapterCount] = useState([]);
  const { slug, slug2 } = useParams();

  const getChapter = async () => {
    try {
      const res = await fetch(`../${slug}/${slug2}/getChapter`, {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-type": "application/json",
        },
        credentials: "include",
      });
      // console.log(typeof res.status);
      if (res.status === 200 || res.status === 304) {
        const data = await res.json();
        console.log(data, "type of data==>", typeof data);
        setChapterGot(true);
        getDataFromG(data.body);

        await setChapter(data);
      } else {
        console.log("error");
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getChapter();
  }, []);

  return (
    <div>
      hi this is chapter
      {chapterGot && (
        <div>
          <div> chapter body: {chapter.body}</div>

          <div>
            {chapterCount &&
              chapterCount.map((prop) => {
                let chapterX = `./chapter-${prop}`;
                return (
                  <>
                    <Link to={chapterX}>chapter-{prop}</Link>
                  </>
                );
              })}
          </div>
        </div>
      )}
    </div>
  );
};

export default Chapter;
