import React, { useContext, useState, useCallback } from 'react';
import { Link } from "react-router-dom";
import { UserContex } from "../App";
import my_logo from "../images/2.svg";

const Navbar = () => {
  const { state, dispatch } = useContext(UserContex);
  const [searchPara, setSearchPara] = useState({ searchBar: "" });
  const { searchBar } = searchPara;
  let name, value;

  const debounce = (func) => {
    let timer;
    return function (...args) {
      const context = this;
      if (timer) clearTimeout(timer);
      timer = setTimeout(() => {
        timer = null
        func.apply(context, args);
      }, 750)
    }
  }

  const getSerachResult = async (e) => {
    e.preventDefault();
    // console.log("getsearch result func called")
    if (e.target.value.length > 2) {// double chck is required to ensure no extra resquest get pass
      console.log("send request to server with value=", e.target.value);
      const res = await fetch("/minor-search", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ searchBar: e.target.value })
      });
      const data = await res.json();
      console.log(data);
    }
  }

  const optimisedVersion = useCallback(debounce(getSerachResult), []);
  const handleInputSearch = async (e) => {
    // let renderTime = new Date()
    name = e.target.name;
    value = e.target.value;
    await setSearchPara({ ...searchPara, [name]: value });
    // console.log("e.value:", value.length)s
    optimisedVersion(e);
  }
  return (
    <>
      <nav>
        <div className="logo">
          <img src={my_logo} alt="" />
        </div>
        <ul>
          <li><Link to="/"><span>Home</span></Link></li>
          <li><Link to="/Super"><span>Super</span></Link></li>
          <li><Link to="/Launch"><span>Launch</span></Link></li>
          {state && <>
            <li><Link to="/about"><span>About</span></Link></li>
            <li><Link to="/logout"><span>Logout</span></Link></li>
          </>
          }
          {state || <>
            <li><Link to="/signUp"><span>SignUp</span></Link></li>
            <li><Link to="/login"><span>Login</span></Link></li>
          </>
          }


        </ul>
        <div className="searchBar">
          <input type="text" name="searchBar" id="searchBar" placeholder="Search here" onChange={handleInputSearch} />
          <button className="btn">Search</button>
        </div>
        <div className="result">
          { }
        </div>
      </nav>

    </>
  )

}

export default Navbar
