import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
const setIntialState = () => { return [] };

const BookEntry = () => {
    let navigate = useNavigate()
    const genreList = ["Action", "Sci-fi", "Comedy", "other"];
    let genreList2 = []; // this will be aquired from backend
    // console.log("found o not==>",genreList2.find(element => element ==="Actin"))
    // function to intial selection
    const updateGenre = (arr) => {
        arr.map((valueX) => {
            document.getElementById("Action").checked = true;
        })
    }

    const [checkedGenre, setCheckedGenre] = useState(setIntialState);
    const [bookExist, setBookExist] = useState(true);
    const [authorised, setAuthorised] = useState(true);

    const [book, setBook] = useState({
        bookId: "", book_Title: "", bookCover_URL: "", volume_Index: "", volume_Title: "", genre: ["1", "2"], author: "", total_Chapter: "", last_Updated_Chapter: "", book_Description: "", likes_Numbers: "", comments_Numbers: "", rating_Average: ""
    });

    const handleChange = (e) => { // changes in check box
        if (e.target.checked === true) {
            setCheckedGenre([...checkedGenre, e.target.value])
            setBook({ ...book, genre: [...checkedGenre, e.target.value] })

        } else {
            setCheckedGenre(checkedGenre.filter((index) => index !== e.target.value))
            setBook({ ...book, genre: (checkedGenre.filter((index) => index !== e.target.value)) })
        }
    }
    let name, value;

    const handleInputBook = (e) => {
        // console.log(e);
        name = e.target.name;
        value = e.target.value;

        setBook({ ...book, [name]: value });
    }

    const getBookEdit = async (e) => {
        setBookExist(true);
        setAuthorised(true);
        e.preventDefault();
        let bookTitle = book.book_Title;
        const res = await fetch("/bookInfo", {

            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            credentials: "include",
            body: JSON.stringify({
                book_Title: bookTitle
            })
        });
        const data = await res.json();
        if (res.status) {
            if (res.status === 402) { setBookExist(false) }
            if (res.status === 401) { setAuthorised(false) }
        }
        // console.log("data==>", data); // showing error/responce
        let { bookId, book_Title, bookCover_URL, volume_Index, volume_Title, genre, author, total_Chapter, last_Updated_Chapter, book_Description, likes_Numbers, comments_Numbers, rating_Average } = data;
        setBook({ bookId, book_Title, bookCover_URL, volume_Index, volume_Title, genre, author, total_Chapter, last_Updated_Chapter, book_Description, likes_Numbers, comments_Numbers, rating_Average })
        // setBook(data)
        genreList2 = data.genre;
        console.log("data.genre==>",typeof(data.genre))
    }

    async function PostBook(e) {
        e.preventDefault();
        const { bookId, book_Title, bookCover_URL, volume_Index, volume_Title, genre, author, total_Chapter, last_Updated_Chapter, book_Description, likes_Numbers, comments_Numbers, rating_Average } = book;
        console.log("book==>", book)
        const res = await fetch("/registerBook", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            credentials: "include",
            body: JSON.stringify({
                bookId, book_Title, bookCover_URL, volume_Index, volume_Title, genre, author, total_Chapter, last_Updated_Chapter, book_Description, likes_Numbers, comments_Numbers, rating_Average
            })
        });
        // console.log(res.body);
        // console.log("fetch sent");
        const data = await res.json();
        console.log("data==>", data); // showing error/responce
        console.log("status==>", res.status);
        if (res.status === 401) { setAuthorised(false) }
        if (res.status === 422 || !data) {
            console.log(data);
        } else {
            // console.log("Error in uploading ");
        }

    }

    return (
        <>
            <div className="back">
                <div id="login-box-long">
                    <form className="register-form" method="POST" id="register-form">
                        <div className="left-long">
                            <h1 className="signupH1">Submit a Book </h1>
                            <input type="number" name="bookId" id="bookId" placeholder="bookId" autoComplete="off" value={book.bookId} onChange={handleInputBook} />
                            <input type="text" name="book_Title" id="book_Title" placeholder="book_Title" autoComplete="off" value={book.book_Title} onChange={handleInputBook} />
                            <button className="btn btn1" name="getBookEdit" value="get Book Edit Info" id="getBookEdit" onClick={getBookEdit} />
                            {bookExist || "Book does not exist, check name and retry"}
                            {authorised || "You are not authorised to edit this book"}
                            <input type="text" name="bookCover_URL" id="bookCover_URL" placeholder="bookCover_URL" autoComplete="off" value={book.bookCover_URL} onChange={handleInputBook} />
                            <input type="text" name="author" id="author" placeholder="author" autoComplete="off" value={book.author} onChange={handleInputBook} />

                            {genreList.map((valueD) => {
                                return <>
                                    <input type="checkbox" onChange={handleChange} id={valueD} name={valueD} value={valueD} checked={book.genre && book.genre.find(element => element === valueD)} />
                                    <label> {valueD} </label>
                                </>
                            })}
                            {/* {updateGenre(genreList2)} */}
                            <br></br>{book.genre && book.genre.map((prop) => {
                                return <><span>{prop} </span></>
                            })}

                            <input type="number" name="total_Chapter" id="total_Chapter" placeholder="total_Chapter" autoComplete="off" value={book.total_Chapter} onChange={handleInputBook} />
                            <input type="number" name="last_Updated_Chapter" id="last_Updated_Chapter" placeholder="last_Updated_Chapter" autoComplete="off" value={book.last_Updated_Chapter} onChange={handleInputBook} />
                            <input type="text" name="book_Description" id="book_Description" placeholder="book_Description" autoComplete="off" value={book.book_Description} onChange={handleInputBook} />
                            <input type="number" name="likes_Numbers" id="likes_Numbers" placeholder="likes_Numbers" autoComplete="off" value={book.likes_Numbers} onChange={handleInputBook} />
                            <input type="number" name="comments_Numbers" id="comments_Numbers" placeholder="comments_Numbers" autoComplete="off" value={book.comments_Numbers} onChange={handleInputBook} />
                            <input type="text" name="rating_Average" id="rating_Average" placeholder="rating_Average" autoComplete="off" value={book.rating_Average} onChange={handleInputBook} />
                            <input type="number" name="volume_Index" id="volume_Index" placeholder="volume_Index" autoComplete="off" value={book.volume_Index} onChange={handleInputBook} />
                            <input type="text" name="volume_Title" id="volume_Title" placeholder="volume_Title" autoComplete="off" value={book.volume_Title} onChange={handleInputBook} />

                            <input type="submit" name="SubmitBook" value="Submit" id="SubmitBook" onClick={PostBook} />

                        </div>
                    </form>

                </div>
            </div >
        </>
    )
}

export default BookEntry