import React, { useEffect, useState, ObjectRow } from 'react'
import { Link,  useParams } from "react-router-dom";


const Book = () => {
    
    const updateChapterCounts = (num) => {
        let rows = [];
        // num = parseFloat(num)
        for (var i = 0; i < num; i++) {
            // note: we are adding a key prop here to allow react to uniquely identify each
            // element in this array. see: https://reactjs.org/docs/lists-and-keys.html
            rows.push(i+1);
        }
        console.log("rows==>", rows)
        return rows;
    }
    const [chapter, setChapter] = useState({ data: "" });
    const [chapterGot, setChapterGot] = useState(false);
    const [chapterCount, setChapterCount] = useState([]);
    const {slug} = useParams();
    const getChapter = async () => {
        try {
            const res = await fetch(`./${slug}/getChapter`, {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-type": "application/json"
                },
                credentials: "include"
            });
            if (res.status !== 200) {
                const error = new Error(res.error);
                throw error;
            } else {
                const data = await res.json();
                console.log(data, "type of data==>", typeof (data))
                setChapter(data);
                setChapterCount(updateChapterCounts(data.last_Updated_Chapter));
                console.log("data.last_Updated_Chapter==>", data.last_Updated_Chapter, "type==", typeof (data.last_Updated_Chapter))
                setChapterGot(true);
            }
        } catch (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        getChapter();
    }, []);

    return (
        <div>
            hi this is book
            {chapterGot &&
                <div>

                    <div>book_Title: {chapter.book_Title}</div>
                    <div>volume: {chapter.volume_Index} {chapter.volume_Title}</div>
                    <div>
                        <br></br>{chapter.genre && chapter.genre.map((prop) => {
                            return <><span>{prop} </span></>
                        })}
                    </div>
                    <img src={chapter.bookCover_URL} alt="no image available" />
                    <div>total_Chapter: {chapter.total_Chapter}</div>
                    <div>last_Updated_Chapter: {chapter.last_Updated_Chapter}</div>
                    <div>book_Description: {chapter.book_Description}</div>
                    <div>likes_Numbers: {chapter.likes_Numbers}</div>
                    <div>comments_Numbers: {chapter.comments_Numbers}</div>
                    <div>rating_Average: {chapter.rating_Average}</div>

                    <div>
                        {
                        chapterCount &&
                            chapterCount.map((prop) => {
                                let chapterX = `./chapter-${prop}`
                                return <><Link to={chapterX}>chapter-{prop}</Link></>
                            })
                        }
                    </div>
                </div>}
        </div>
    )
}

export default Book
