import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";


const ChapterEntry = () => {
    let navigate = useNavigate()

    const [chapter, setChapter] = useState({
        bookName: "", chapterName: "", chapterIndex: "", chapterBody: ""
    });
    let name, value;

    const handleInputCh = (e) => {
        // console.log(e);
        name = e.target.name;
        value = e.target.value;

        setChapter({ ...chapter, [name]: value });
    }

    async function PostChapter(e) {
        e.preventDefault();
        const {bookName, chapterName, chapterIndex, chapterBody} = chapter;
        let id = chapterIndex;
        console.log("chapter==>",chapter)
        const res = await fetch("/registerChapter", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            credentials: "include",
            body: JSON.stringify({
                id,bookName, chapterName, chapterIndex, chapterBody
            })
        });

        // console.log(res.body);
        // console.log("fetch sent");
        const data = await res.json();
        console.log("data==>", data); // showing error/responce
        console.log("status==>", res.status);
        if (res.status === 422 || !data) {
            console.log(data);
        } else {
            console.log("Error in uploading ");
        }

    }

    return (
        <>
            <div className="back">
                <div id="login-box-long">
                    <form className="register-form" method="POST" id="register-form">
                        <div className="left-long">
                            <h1 className="signupH1">Submit a Chapter </h1>
                            <input type="text" name="bookName" id="bookName" placeholder="bookName" autoComplete="off" value={chapter.bookName} onChange={handleInputCh} />
                            <input type="text" name="chapterName" id="chapterName" placeholder="chapterName" autoComplete="off" value={chapter.chapterName} onChange={handleInputCh} />
                            <input type="text" name="chapterIndex" id="chapterIndex" placeholder="chapterIndex" autoComplete="off" value={chapter.chapterIndex} onChange={handleInputCh} />
                            <input type="textarea" name="chapterBody" id="chapterBody" placeholder="chapterBody" autoComplete="off" value={chapter.chapterBody} onChange={handleInputCh} />

                            <input type="submit" name="SubmitChapter" value="Submit" id="SubmitChapter" onClick={PostChapter} />
                        </div>
                    </form>
                    <div className="right">

                    </div>
                    <div className="or">OR</div>
                </div>
            </div>
        </>
    )
}

export default ChapterEntry